const $siteList = $('.siteList')
const $lastLi = $('li.last')

const siteData = localStorage.getItem('data')
const dataObject = JSON.parse(siteData)    //è¯»å–è¦å˜æˆJSONå¯¹è±¡è¿›è¡Œæ“ä½œ
const hashMap = dataObject || [{ logo: 'A', url: 'https://www.acfun.cn' }, { logo: 'B', url: 'https://www.bilibili.com' }]




const simplifyUrl = (url) => {
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '') // åˆ é™¤ / å¼€å¤´çš„å†…å®¹
}
const render = () => {
    $siteList.find('li:not(.last)').remove()
    hashMap.forEach((node, index) => {
        const $li = $(`<li>
                <div class="site">
                    <div class="logo">${node.logo}</div>
                    <div class="link">${simplifyUrl(node.url)}</div>
                    <div class="close">
                      <svg class="icon1">
                        <use xlink:href="#icon-close1"></use>
                          </svg>
                              </div>
                </div>
            </li>`).insertBefore($lastLi)
        $li.on('click', () => {
            window.open(node.url)
        })
        $li.on('click', '.close', (e) => {
            e.stopPropagation() // é˜»æ­¢å†’æ³¡
            hashMap.splice(index, 1)
            render()
        })
    })
}

render()

$('.icon').on('click', () => {
    let url = window.prompt('è¯·é—®ä½ è¦æ·»åŠ çš„ç½‘å€æ˜¯å•¥ï¼Ÿ')
    if (!url) { return }
    else if (url.indexOf('http') !== 0) {
        url = 'https://' + url
    }

    hashMap.push({
        logo: simplifyUrl(url)[0].toUpperCase(),  //æœ€åŽä»¥å­—ç¬¦ä¸²çš„æ ·å¼è¾“å‡º
        url: url
    })
    render()
})

window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap)    //å­˜å‚¨è¦å°†å…¶è½¬åŒ–ä¸ºå­—ç¬¦ä¸²çš„æ ·å¼  GlobalEventHandlers.onbeforeunload
    localStorage.setItem('data', string)
}

$(document).on('keypress', (e) => {
    const { key } = e
    for (let i = 0; i < hashMap.length; i++) {
        if (hashMap[i].logo.toLowerCase() === key) {
            window.open(hashMap[i].url)
        }
    }
})